.NavigationItem{
    margin: 10px 0px;
    box-sizing: border-box;
    width: 100%;
    display: block;
}

.NavigationItem a{
    color: #8F5C2C;
    text-decoration: none;
    box-sizing: border-box;
    display: block;
}

.NavigationItem a:hover,
.NavigationItem a:active,
.NavigationItem a.active{
    color:  #40A4C8;
}

@media (min-width: 500px)
{
    .NavigationItem{
        height: 100%;
        align-items: center;
    }
    
    .NavigationItem a{
        color: white;
        height: 100%;
        padding: 16px 10px;
        border-bottom: 4px solid transparent;
        display: flex;
    }
    
    .NavigationItem a:hover,
    .NavigationItem a:active,
    .NavigationItem a.active{
        color: white;
        background-color: #8F5C2C;
        border-bottom: 4px solid #40A4C8;
    }
}